@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
$--header-border-color: #2a2c31;
$--header-background-color: #0f0f0f;

@include b(header) {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  user-select: none;
  background-color: $--header-background-color;
  box-shadow: 0 1px 3px rgba(26, 26, 26, 0.9);
  transition: left 0.3s ease;

  @at-root {
    %inline-block {
      display: inline-block;
      height: 100%;
      vertical-align: top;
    }

    %common-icons {
      @include background-image(url(~@/assets/img/common/common_icons.png));
    }

    %position-box {
      position: absolute;
      top: 100%;
      z-index: 1;
      visibility: hidden;
      background-color: #fff;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.5);
      opacity: 0;
      transition: all 0.3s ease-in-out;
      transform: translateY(10px);
    }

    %position-box-hover {
      visibility: visible;
      opacity: 1;
      transform: translateY(0);
    }
  }

  @include e(inner) {
    position: relative;
    min-width: 950px;
    height: $--header-height;
    margin: 0 auto;
    line-height: $--header-height - 1;
    white-space: nowrap;
    background: no-repeat top center;
    border-bottom: 1px solid $--header-border-color;

    @include background-image(url(~@/assets/img/common/header_bg.png));
  }

  @include e(left) {
    float: left;
    height: 100%;
  }

  @include e(side-bar-switch) {
    @extend %inline-block;

    width: 115px;
    font-size: 13px;
    font-weight: 700;
    color: #697081;
    border-right: 1px solid $--header-border-color;
    transition: all 0.2s ease-in-out;

    &:hover {
      color: #525866;

      @include e(icon-side-bar) {
        background-position: -15px 0;
      }
    }
  }

  @include e(icon-side-bar) {
    display: inline-block;
    width: 15px;
    height: 13px;
    margin-right: 10px;
    vertical-align: middle;
    transition: all 0.2s ease-in-out;

    @extend %common-icons;
  }

  @include e(partners) {
    @extend %inline-block;

    margin-left: 30px;
  }

  @include e(partner-item) {
    $--list: (0, 34), (1, 25), (2, 35), (3, 32), (4, 27);

    @extend %inline-block;

    overflow: hidden;
    white-space: nowrap;
    transition: max-width 0.4s ease-in-out;
    will-change: max-width;

    @each $i, $width in $--list {
      &.item-#{$i} {
        max-width: $width + 8px;

        &:hover { // 加上字体大小*字数
          max-width: $width + 12 * 7 + 8 + 8px;
        }
      }
    }

    &:hover {
      @include e(partner-logo) {
        opacity: 1;
      }
    }
  }

  @include e(partner-logo) {
    $--list: (0, 34, 0), (1, 25, -41), (2, 35, -74), (3, 32, -116), (4, 27, -154);

    display: inline-block;
    height: 38px;
    margin-right: 8px;
    vertical-align: middle;
    background: no-repeat left top/auto 100%;
    opacity: 0.8;
    transition: opacity 0.2s ease;

    @include background-image(url(~@/assets/img/components/header/partners_logo.png));

    @each $i, $width, $position in $--list {
      &.logo-#{$i} {
        width: $width + px;
        background-position: $position + px 0;
      }
    }
  }

  @include e(partner-title￿) {
    margin-right: 8px;
    font-size: 12px;
    color: $--color-primary;
  }

  @include e(logo) {
    position: absolute;
    top: ($--header-height - 40) / 2;
    left: 50%;
    width: 280px;
    height: 40px;
    margin-left: -140px;
    background: no-repeat left -48px;
    transition: all 0.4s ease-in-out;

    @include background-image(url(~@/assets/img/common/kf_logos.png));

    @media screen and (max-width: 1200px) {
      width: 50px;
      margin-left: -25px;
    }
  }

  @include e(right) {
    float: right;
    height: 100%;
  }

  @include e(message) {
    position: relative;
    display: inline-block;
    height: 100%;
    margin-right: 25px;
    cursor: pointer;

    &:hover {
      @include e(message-box) {
        @extend %position-box-hover;
      }
    }
  }

  @include e(icon-message) {
    display: inline-block;
    width: 20px;
    height: 24px;
    margin: 10px;
    vertical-align: middle;
    background-position: 0 -15px;

    @extend %common-icons;
  }

  @include e(message-box) {
    @extend %position-box;

    right: -97px;
    width: 380px;
    cursor: default;
  }

  @include e(message-header) {
    height: 44px;
    padding: 0 16px;
    font-size: 14px;
    line-height: 44px;
    color: #181818;
    border-bottom: 1px solid #deddda;
  }

  @include e(message-content) {
    height: 248px;
    overflow: auto;
    text-align: center;
  }

  @include e(no-message) {
    margin-top: 70px;
    font-size: 12px;
    line-height: 1.15;
    color: #b0b0b0;
    text-align: center;
  }

  @include e(icon-no-message) {
    display: inline-block;
    width: 66px;
    height: 80px;
    margin-bottom: 10px;
    vertical-align: top;
    background-position: -212px 0;

    @extend %common-icons;
  }

  @include e(login-before) {
    @extend %inline-block;

    font-size: 13px;
    color: #697081;
  }

  @include e(try-link) {
    margin-right: 22px;
    color: $--color-primary;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.7;
    }
  }

  @include e(sign) {
    @extend %inline-block;

    position: relative;
    width: 190px;
    overflow: hidden;
    font-size: 13px;
    font-weight: 700;
    color: #fff;
    text-align: center;
    background-color: $--color-primary;
    transition: background-color 0.3s ease;

    &::after {
      position: absolute;
      top: -10%;
      left: -100%;
      width: 100%;
      height: 100%;
      content: "";
      background-color: hsla(0, 0%, 100%, 0.2);
      transform: rotate(-45deg);
    }

    &:hover {
      background-color: #ba8d4d;

      &::after {
        left: 100%;
        transition: left 0.6s ease;
      }
    }
  }

  @include e(sign-link) {
    margin: 0 4px;
    transition: opacity 0.3s;

    &:hover {
      opacity: 0.8;
    }
  }

  @include e(login-after) {
    @extend %inline-block;

    padding: 0 30px 0 18px;
    font-size: 13px;
    color: #fff;
    border-left: 1px solid $--header-border-color;
  }

  @include e(button) {
    display: inline-block;
    width: 84px;
    height: 32px;
    margin-right: 8px;
    font-size: 12px;
    line-height: 32px;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    background-color: #323232;
    border-radius: 4px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2), inset 0 1px 0 #4b4b4b;
    transition: background 0.2s linear;

    &:hover {
      @include button-background-solid(false);

      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.2);
    }

    &:nth-child(2) {
      margin-right: 14px;
    }
  }

  @include e(button-icon) {
    display: inline-block;
    margin-right: 4px;
    vertical-align: middle;

    @extend %common-icons;

    @include m(deposit) {
      width: 20px;
      height: 21px;
      background-position: 0 -85px;
    }

    @include m(withdraw) {
      width: 19px;
      height: 20px;
      background-position: -22px -85px;
    }
  }

  @include e(user) {
    @extend %inline-block;

    position: relative;
    cursor: pointer;

    &:hover {
      @include e(icon-menu) {
        background-position: -38px -23px;
      }

      @include e(user-menu) {
        @extend %position-box-hover;
      }
    }
  }

  @include e(user-info) {
    display: inline-block;
    line-height: 20px;
    color: #697081;
    text-align: right;
    vertical-align: middle;
  }

  @include e(user-balance) {
    font-weight: 700;
    color: $--color-primary;
  }

  @include e(user-avatar) {
    display: inline-block;
    width: 38px;
    height: 38px;
    margin: 0 8px;
    vertical-align: middle;
    background-position: 0 -204px;

    @extend %common-icons;
  }

  @include e(icon-menu) {
    display: inline-block;
    width: 15px;
    height: 13px;
    vertical-align: middle;
    background-position: -23px -23px;
    transition: all 0.2s ease-in-out;

    @extend %common-icons;
  }

  @include e(user-menu) {
    @extend %position-box;

    right: 0;
    width: 250px;
    cursor: default;
    background-color: #fcfcfc;
  }

  @include e(menu-item) {
    display: block;
    height: 70px;
    padding-left: 20px;
    font-size: 16px;
    font-weight: 700;
    line-height: 70px;
    color: #181818;
    background-color: #fcfcfc;
    transition: background-color 0.3s ease-in-out;

    & + & {
      border-top: 1px solid #deddda;
    }

    &:last-child {
      color: #92918d;
    }

    &:hover {
      background-color: #efeeec;

      @include e(menu-icon) {
        opacity: 1;
        transition: all 0.3s ease-in-out;
        transform: translateX(0);
      }
    }
  }

  @include e(menu-icon) {
    float: right;
    margin-top: (70px - 10) / 2;
    margin-right: 20px;
    opacity: 0;
    transform: translateX(-10px);

    @extend %common-icons;

    @include m(arrow) {
      width: 6px;
      height: 10px;
      background-position: -45px -89px;
    }

    @include m(logout) {
      width: 16px;
      height: 14px;
      background-position: -189px -37px;
      opacity: inherit;
      transform: none;
    }
  }

  &.open-side-bar {
    left: $--side-bar-width;
  }
}

@include b(nav) {
  display: flex;
  max-width: $--content-width;
  height: $--nav-bar-height;
  margin: 0 auto;
  font-size: 15px;
  line-height: $--nav-bar-height;
  text-align: center;
  white-space: nowrap;

  @include e(item) {
    position: relative;
    flex-grow: 1;

    &::before {
      position: absolute;
      top: 50%;
      right: 0;
      width: 1px;
      height: 10px;
      margin-top: -5px;
      content: '\20';
      border-right: 1px dotted #525866;
    }

    &:last-child {
      &::before {
        display: none;
      }
    }
  }

  @include e(link) {
    position: relative;
    display: inline-block;
    padding: 0 10px;
    vertical-align: top;
    cursor: pointer;
    transition: color 0.2s ease;

    &::before {
      position: absolute;
      right: 50%;
      bottom: 0;
      left: 50%;
      height: 2px;
      content: '\20';
      background-color: $--color-primary;
      transition: all 0.2s ease;
    }

    &:hover,
    &.active {
      color: $--color-primary;

      &::before {
        right: 0;
        left: 0;
      }
    }
  }

  /*&:hover {
    @include e(link) {
      &:not(:hover) {
        color: inherit;

        &::before {
          width: 0;
        }
      }
    }
  }*/
}
