@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
$--z-index: 99;

@include b(side-bar) {
  @include e(inner) {
    position: fixed;
    top: 0;
    left: -$--side-bar-width;
    z-index: $--z-index;
    width: $--side-bar-width;
    height: 100%;
    overflow: hidden;
    background-color: #3a3a3a;
    transition: left 0.3s ease;
  }

  @include e(scroll) {
    width: $--side-bar-width+20px;
    height: 100%;
    overflow: auto;
  }

  @include e(content) {
    width: $--side-bar-width;
  }

  @include e(mask) {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $--z-index - 1;
    background-color: rgba(0, 0, 0, 0.7);
  }

  &.open {
    @include e(inner) {
      left: 0;
    }
  }
}
